<template>
  <button class="button" type="button" :class="{'button--small':small, 'button--normal':normal}">
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'Button',
  props: {
    small: Boolean,
    normal: Boolean
  }
}
</script>



