    <div class="AboutMeForm__input__next">
      <img src="../images/component/AboutMeForm/next.svg" width="50px" height="32px;" >
      <p class="AboutMeForm__input__p">下一个</p>
    </div>
    <div class="AboutMeForm__button">
      <qybutton small >暂无</qybutton>
      <qybutton small>下一步</qybutton>
    </div>
  </div>
</template>

<script>
import dropdown from './dropdown'
import qybutton from './Button.vue'
export default {
  name: 'AboutMeForm',
  components: {
    dropdown,
    qybutton
  },
  data () {
    return {
      InputHand: false,
      Type: ['项目类别', '项目类别', '项目类别', '项目类别'],
      duration: ['0.5年-1年', '0.5年-1年', '0.5年-1年', '0.5年-1年', '0.5年-1年'],
      Category: ['商科', '商科', '商科', '商科']
    }
  }

}
</script>

<style scoped>
.tr{
  animation: sc 1s linear ;
}
@keyframes sc{
  from{
    transform: scaleY(0)
  }
  to{
    transform: scaleY(100%)
  }
}



.AboutMeForm{
  width: 480px;
  height: 450px;
  position: relative;
}
.AboutMeForm__bg{
  position: absolute;
  left: 0;
}
.AboutMeForm__add{
  width: 160px;
  height: 40px;
  border: 1px dashed rgba(177, 217, 218, 0.5) ;
  position: absolute;
  left: 30%;
  top: 24px;
  cursor: pointer;
}

.AboutMeForm__add:hover{
  background-color: rgba(177, 217, 218, 0.08);
}
.AboutMeForm__add__icon{
  padding: 12px 18px;
  float: left;
}
.AboutMeForm__add__title{
  color: rgb(177, 217, 218);
  text-align: center;
  line-height: 40px;
}
.AboutMeForm__delete{
  position: absolute;
  right: 50px;
  top: 24px;
  width: 45px;
  height: 45px;
  border: 1px solid rgba(177, 217, 218, 0.5);
  border-radius: 50%;
  cursor: pointer;
}
.AboutMeForm__delete:hover{
  background-color: rgba(177, 217, 218, 0.3);
}
.AboutMeForm__delete__icon{
  width: 15px;
  height: 15px;
  padding: 15px;
}
.AboutMeForm__input{
  width: 280px;
  height: 300px;
  background-color: rgba(177, 217, 218, 0.08);
