}

.AboutMeForm__input__Time{
  margin: 20px;
}
.AboutMeForm__input__Time span{
  font-size: 12px;
  color: rgb(177, 217, 218)
}
.AboutMeForm__input__Time--season{
  position: relative;
  float: left;
}
.AboutMeForm__input__Time--year{
  position: relative;
  float: left;
  margin-left: 20px;
}
.AboutMeForm__button{
  position: absolute;
  bottom: 30px;
  left: 25%;
}
.AboutMeForm__button button {
  margin-left: 30px;
}


.AboutMeForm__input__pre{
  position: absolute;
  left: 20px;
  top: 50%;
  cursor: pointer;
  color:rgba(177, 217, 218,0.5);
}
.AboutMeForm__input__pre:hover{
  color:rgb(177, 217, 218);
}
.AboutMeForm__input__next{
  position: absolute;
  right: 20px;
  top: 50%;
  cursor: pointer;
  color:rgba(177, 217, 218,0.5);
}
.AboutMeForm__input__next:hover{
  color:rgb(177, 217, 218);
}
.AboutMeForm__input__p{
  font-size: 12px;
  margin-top: 12px;
  margin-left: 5px;
}

</style>



